import axios from 'axios'

export default class VentaService {
  //  url = 'http://localhost:8080/ventas/';
  url = 'http://localhost:3000/ventas/';
	createdVenta(venta) {
    Date.now(); 
    const timeElapsed = Date.now();
    const today = new Date(timeElapsed);  
    venta.date= today.toISOString();    
    return axios.post(this.url,venta).then(res=>
        res.data        
      ); 
    }
    getVentas() {  
        return axios.get(this.url).then(res=>
          res.data        
        ); 
      }

    updateVenta(venta) {
      return axios.patch(this.url+venta.id,venta).then(res=>
      res.data        
    ); 
  }
  getVenta(venta) {
    return axios.get(this.url+venta.id).then(res=>
    res.data        
    ); 
  }
  deleteVenta(venta) {
    return axios.delete(this.url+venta.id).then(res=>
    res.data        
  ); 
}
}
